<template>
  <ChartContainer :width="width" :height="height" :data="data" :x="x" :y="y" :margin="margin">
    <XAxis class="time" align="bottom" format="%Y-%m" :annotation="timeAnnotation"/>
    <ChartCandlestick :y="yCandlestick">
      <ChartMA class="sma-5" color="#1f77b4" type="sma" :count="5"/>
      <ChartMA class="sma-10" color="#aec7e8" type="sma" :count="10"/>
      <ChartMA class="sma-20" color="#ff7f0e" type="sma" :count="20"/>
      <YAxis align="right" class="axis-candlestick" :annotation="ohlcAnnotation" :ticks="6"/>
    </ChartCandlestick>
    <ChartVolume :y="yVolume">
      <YAxis align="right" class="axis-volume" :ticks="3" format=",.3s" :annotation="volumeAnnotation"/>
    </ChartVolume>
    <Crosshair/>
  </ChartContainer>
</template>

<script>
  import $ from 'jquery'

  import ChartContainer from './ChartContainer'
  import ChartCandlestick from './ChartCandlestick'
  import ChartVolume from './ChartVolume'
  import YAxis from './YAxis'
  import YPercentAxis from './YPercentAxis'
  import XAxis from './XAxis'
  import Crosshair from './Crosshair'
  import ChartMA from './ChartMA'

  export default {
    name: 'KLine',
    props: {
      width: Number,
      height: Number,
      data: {
        type: Array,
        default: []
      }
    },
    data() {
      return {
        margin: {
          top: 24, left: 10, bottom: 40, right: 60
        }
      }
    },
    computed: {
      viewWidth() {
        return this.width - this.margin.left - this.margin.right
      },
      viewHeight() {
        return this.height - this.margin.top - this.margin.bottom
      },
      x() {
        return [0, this.viewWidth]
      },
      y() {
        return [this.viewHeight, 0]
      },
      yCandlestick() {
        return [this.viewHeight - 100, 0]
      },
      yVolume() {
        return [this.viewHeight, this.viewHeight - 80]
      },
      timeAnnotation() {
        return {
          width: 80,
          orient: 'bottom',
          format: '%Y-%m-%d',
          translate: [0, this.viewHeight]
        }
      },
      ohlcAnnotation() {
        return {
          orient: 'right',
          format: ',.2f'
        }
      },
      percentAnnotation() {
        return {
          orient: 'right',
          translate: [this.viewWidth, 0]
        }
      },
      volumeAnnotation() {
        return {
          orient: 'right',
          format: ',.3s'
        }
      }
    },
    components: {
      ChartContainer,
      ChartCandlestick,
      ChartVolume,
      XAxis,
      YAxis,
      YPercentAxis,
      Crosshair,
      ChartMA
    },
    mounted() {
      this.$nextTick(() => $(window).trigger('resize'))
    },
    updated() {
      console.log('kline updated')
    }
  }
</script>



