    },
    mounted: function () {
      this.changeData()
    },
    methods: {
      changeData() {
        var _self = this
        _self.changeHandler = setInterval(function () {
          _self.sellDetails.forEach(function (val) {
            val.num++
          })
          _self.sellOneDetails.pop()
        }, 5000)
      },
      stop() {
        clearInterval(this.changeHandler)
      }
    }
  }
</script>

<style scoped lang="less">
  .dashboard {
    width: 100vw;
    height: 100vh;

    display: flex;
  }
</style>



