<template>
  <ChartContainer :width="width" :height="height" :data="data" :x="x" :y="y" :margin="margin">
    <XAxis class="time" align="bottom" format="%Y-%m" :annotation="timeAnnotation"/>
    <ChartMA class="f-line" color="#fff" type="sma" :count="1" :y="yCandlestick">
      <YAxis class="axis-candlestick" :annotation="ohlcAnnotation" :ticks="6"/>
      <YPercentAxis align="right" :ticks="6" format="+.1%" :annotation="percentAnnotation"/>
    </ChartMA>
    <ChartVolume :y="yVolume">
      <YAxis align="right" class="axis-volume" :ticks="3" format=",.3s" :annotation="volumeAnnotation"/>
    </ChartVolume>
    <Crosshair/>
  </ChartContainer>
</template>

<script>
  import KLine from './KLine'
  import ChartContainer from './ChartContainer'
  import ChartCandlestick from './ChartCandlestick'
  import ChartVolume from './ChartVolume'
  import YAxis from './YAxis'
  import YPercentAxis from './YPercentAxis'
  import XAxis from './XAxis'
  import Crosshair from './Crosshair'
  import ChartMA from './ChartMA'

  export default {
    name: 'FLine',
    mixins: [KLine],
    data() {
      return {
        margin: {
          top: 24, left: 40, bottom: 40, right: 60
        }
      }
    },
    computed: {
      timeAnnotation() {
        return {
          width: 80,
          orient: 'bottom',
          format: '%Y-%m-%d',
          translate: [0, this.viewHeight]
        }
      },
      ohlcAnnotation() {
        return {
          orient: 'left',
          format: ',.2f'
        }
      },
      percentAnnotation() {
        return {
          orient: 'right',
          translate: [this.viewWidth, 0]
        }
      }
    },
    components: {
      ChartContainer,
      ChartCandlestick,
      ChartVolume,
      XAxis,
      YAxis,
      YPercentAxis,
      Crosshair,
      ChartMA
    }
  }
</script>



