import Main from '../../main'
import Common from '../common/common'

export default {
  handleDataTraDet(message, datajson) { // 处理买卖及详情
    // console.log(Main)
    // Main.getRequest()
    let traDetObj = {} // 用于返回的数据对象（存于全局变量中）
    traDetObj.buyDetails = [] // 买方明细
    traDetObj.sellDetails = [] // 买方明细
    // let tradeList = datajson.trade // 成交数据

    let buyarry = datajson.buy // 后台接收的买方json
    buyarry.forEach(function(item, index) {
      let buyBean = {}
      buyBean.price = item.p
      buyBean.num = item.v
      traDetObj.buyDetails.push(buyBean)
    })
    let salearry = datajson.sale // 后台接收的卖方的json
    salearry.forEach(function(item, index) {
      let saleBean = {}
      saleBean.price = item.p
      saleBean.num = item.v
      traDetObj.sellDetails.push(saleBean)
    })
    Main.setGlobalcache_tradet(message, traDetObj)
  },
  handleDataKLine(datajson) {
    let GlobalcacheKline = Main.getGlobalcache_kline()  // 取出已存储的K线 分时线数据
    // console.log(GlobalcacheKline)
    let GlobalcacheKlineLast = null  // 最后一条K线 分时线对象
    if (GlobalcacheKline != null && GlobalcacheKline.length > 0) { // 取出最后一条K线 分时线数据
      GlobalcacheKlineLast = GlobalcacheKline[GlobalcacheKline.length - 1]
    }
    let GlobalcacheKlineLastNext = null // 建立的最新一条K线 分时线对象
    let tradeList = datajson.trade // 成交数据
    // console.log(tradeList)
    for (let i = 0; i < tradeList.length; i++) {
      let tradeTime = Common.handleTimeFull(tradeList[i].t) // 实时推送的成交时间
      tradeTime = Common.handleTimeZeroM(tradeTime)
      let tradeMinute = Common.jqMinuteByTime(tradeTime) // 成交时间的分钟
      // console.log(GlobalcacheKlineLast != null)
      if (GlobalcacheKlineLast != null) {
        // console.log(tradeMinute === Common.jqMinuteByTime(GlobalcacheKlineLast.tradeTime))
        if (tradeMinute === Common.jqMinuteByTime(GlobalcacheKlineLast.tradeTime)) { // 成交时间的分钟等于最后一条K线 分时线的分钟
          if (GlobalcacheKlineLast.low > tradeList[i].p) {
            GlobalcacheKlineLast.low = tradeList[i].p
          } else if (GlobalcacheKlineLast.high < tradeList[i].p) {
            GlobalcacheKlineLast.high = tradeList[i].p
          }
          GlobalcacheKlineLast.close = tradeList[i].p
          GlobalcacheKlineLast.volume = Number(GlobalcacheKlineLast.volume) + Number(tradeList[i].v)
          GlobalcacheKlineLast.handleValue = GlobalcacheKlineLast.handleValue + Common.chengMoney(tradeList[i].p, tradeList[i].v)
          GlobalcacheKlineLast.value = Common.chuMoney(GlobalcacheKlineLast.handleValue)
          // console.log(GlobalcacheKlineLast)
        } else { // 成交时间的分钟不等于最后一条K线 分时线的分钟
          if (GlobalcacheKlineLastNext != null) {
            if (GlobalcacheKlineLastNext.low > tradeList[i].p) {
              GlobalcacheKlineLastNext.low = tradeList[i].p
            } else if (GlobalcacheKlineLastNext.high < tradeList[i].p) {
              GlobalcacheKlineLastNext.high = tradeList[i].p
            }
            GlobalcacheKlineLastNext.close = tradeList[i].p
            GlobalcacheKlineLastNext.volume = Number(GlobalcacheKlineLastNext.volume) + Number(tradeList[i].v)
            GlobalcacheKlineLastNext.handleValue = GlobalcacheKlineLastNext.handleValue + Common.chengMoney(tradeList[i].p, tradeList[i].v)
            GlobalcacheKlineLastNext.value = Common.chuMoney(GlobalcacheKlineLastNext.handleValue)
          } else {
            let KLineObjNext = {}
            let openNext = tradeList[i].p // 开盘价格
            KLineObjNext.timestamp = Common.handleTime(tradeTime) // 毫秒时间戳
            KLineObjNext.tradeTime = tradeTime // 时间格式 （2012-12-25 20:11:11）
            KLineObjNext.open = openNext // 开盘
            KLineObjNext.low = openNext  // 最低
            KLineObjNext.high = openNext // 最高
            KLineObjNext.close = openNext // 收盘
            KLineObjNext.volume = tradeList[i].v // 总成交量
            KLineObjNext.handleValue = Common.chengMoney(tradeList[i].p, tradeList[i].v) // 扩大后的总成交额
            KLineObjNext.value = Common.chuMoney(KLineObjNext.handleValue) // 总成交额
            KLineObjNext.volume1 = 0 // 小
            KLineObjNext.value1 = 0
            KLineObjNext.volume2 = 0 // 中
            KLineObjNext.value2 = 0
            KLineObjNext.volume3 = 0 // 大
            KLineObjNext.value3 = 0
            GlobalcacheKlineLastNext = KLineObjNext
          }
        }
      } else { // 第一次请求 且在9:30
        let open = tradeList[i].p // 开盘价格
        let KLineObj = {}
        KLineObj.timestamp = Common.handleTime(tradeTime) // 毫秒时间戳
        KLineObj.tradeTime = tradeTime // 时间格式 （2012-12-25 20:11:11）
        KLineObj.open = open // 开盘
        KLineObj.low = open  // 最低
        KLineObj.high = open // 最高
        KLineObj.close = open // 收盘
        KLineObj.volume = tradeList[i].v // 总成交量
        KLineObj.handleValue = Common.chengMoney(tradeList[i].p, tradeList[i].v) // 扩大后的总成交额
        KLineObj.value = Common.chuMoney(KLineObj.handleValue) // 总成交额
        KLineObj.volume1 = 0 // 小
        KLineObj.value1 = 0
        KLineObj.volume2 = 0 // 中
        KLineObj.value2 = 0
        KLineObj.volume3 = 0 // 大
        KLineObj.value3 = 0
        GlobalcacheKlineLast = KLineObj
      }
    }

    if (GlobalcacheKlineLastNext != null) {
      if (GlobalcacheKline != null && GlobalcacheKline.length > 0) {
        GlobalcacheKline[GlobalcacheKline.length - 1] = GlobalcacheKlineLast
        GlobalcacheKline.push(GlobalcacheKlineLastNext)
      } else {
        GlobalcacheKline.push(GlobalcacheKlineLast)
        GlobalcacheKline.push(GlobalcacheKlineLastNext)
      }
    } else {
      if (GlobalcacheKline != null && GlobalcacheKline.length > 0) {
        GlobalcacheKline[GlobalcacheKline.length - 1] = GlobalcacheKlineLast
      } else {
        GlobalcacheKline.push(GlobalcacheKlineLast)
      }
    }
    Main.setGlobalcache_kline(GlobalcacheKline)
  }
}



