import DayLine from '../datamanager/dayline'

let websocket = null
let message = 'websocket open'
const webSocketurl = 'ws://192.168.132.82:8080/stockdataserver/websocket'
if ('WebSocket' in window) {
  try {
    websocket = new WebSocket(webSocketurl)
  } catch (e) {
    console.log(e)
  }
  window.onbeforeunload = function () {
    websocket.close()
    message = 'websocket closed'
  }
} else {
  message = '当前浏览器 Not support websocket'
}
websocket.onmessage = function (event) {
  let datajson = JSON.parse(event.data)
  DayLine.handleDataTraDet(message, datajson) // 处理买卖及详情
  DayLine.handleDataKLine(datajson) // K线 分时线
}



