<template>
  <div class="stock-line">
    <StockMenu @changeType="onChangeType"/>
    <KLine v-if="type === 1" :width="width" :height="height" :data="data"/>
    <FLine v-if="type === 0" :width="width" :height="height" :data="data"/>
  </div>
</template>

<script>
  import $ from 'jquery'
  import _ from 'lodash'

  import {parseData, pushData} from './chart/mock'
  import StockMenu from './chart/StockMenu'
  import KLine from './chart/KLine'
  import FLine from './chart/FLine'

  export default {
    name: 'StockLine',
    data() {
      return {
        type: 0,
        width: 0,
        height: 0,
        top: 24,
        data: []
      }
    },
    components: {
      KLine,
      FLine,
      StockMenu
    },
    methods: {
      onChangeType(type) {
        this.type = type
      },
      onResize() {
        const vm = this
        const win = $(window)

        vm.width = win.width() - 300
        vm.height = win.height() / 2 - this.top

        if (vm.width < 640) {
          vm.width = 640
        }

        if (vm.height < 375) {
          vm.height = 375 - this.top
        }

        vm.data = parseData(this.width / 10)
      }
    },
    beforeMount() {
      $(window).on('resize', _.debounce(this.onResize, 100))
      let index = 0
      this.handle = setInterval(() => {
        // 10次变换后添加一条数据
        this.data = pushData(this.data, !(index++ % 3))
      }, 1000)
    }
  }
</script>

<style scoped lang="less">
  .stock-line {
    display: flex;
    flex: auto;
    flex-direction: column;
  }
</style>



