import * as d3 from 'd3'
import addDays from 'date-fns/add_days'

window.d3 = d3

/**
 * 原始数据
 */
export const originData = require('../data.csv')

/**
 * 处理数据
 * @param count
 */
export function parseData(count) {
  count = parseInt(count)
  let length = originData.length
  let begin = originData.length - count
  begin = begin > -1 ? begin : 0

  return originData.map(d => {
    return {
      date: d3.timeParse('%d-%b-%y')(d.Date),
      open: +d.Open,
      high: +d.High,
      low: +d.Low,
      close: +d.Close,
      volume: +d.Volume
    }
  }).sort((a, b) => d3.ascending(d(a), d(b)))
    .slice(begin, length)

  function d(a) {
    return a.date
  }
}

export function pushData(origin, next) {
  let last = origin.length ? origin[origin.length - 1] : null
  let lastDay = last ? last.date : new Date()
  let percent = 1 + (0.08 * Math.random() - 0.04)
  let close = last.close * percent
  let base = 10000000

  // 改变当天数据
  if (!next) {
    last.close = close
    last.volume = last.volume + base * Math.random() * 3
    if (close < last.low) {
      last.low = close
    } else if (close > last.high) {
      last.high = close
    }
  } else {
    // 添加新的一天数据
    let temp = origin.concat([{
      date: addDays(lastDay, 1),
      open: last.close,
      close: close,
      low: close,
      high: close,
      volume: base
    }])

    // 去掉第一个数据
    temp.shift()
    return temp
  }

  return origin.concat([])
}



