const minute = 60 // 1分钟的60秒
const perGu = 100 // 没笔等于100股
const benshu = 10000 // 金额扩大的倍数
export default {
  handleTime(time) { // 将时间转化成秒(2012-12-25 20:11:11)
    // Time = (new Date("2012/12/25 20:11:11")).getTime(); //得到毫秒数
    // 不是上面的格式需要先转化成这样的格式
    time = time.replace(new RegExp('-', 'gm'), '/')
    let starttimeMiao = (new Date(time)).getTime() / 1000
    return starttimeMiao
  },
  handleTimeFull(etime) {  // 将当前时间的年月日和传的时分拼一起
    let now = new Date()
    let ytime = now.getFullYear() + '-'
    let mtime = ((now.getMonth() + 1) < 10 ? '0' : '') + (now.getMonth() + 1) + '-'
    let dtime = (now.getDate() < 10 ? '0' : '') + now.getDate()
    etime = ytime + mtime + dtime + ' ' + etime
    return etime
  },
  handleTimeZeroM(time) { // 将时间转化成(2012-12-25 20:11:11 == 2012-12-25 20:11:00)
    time = time.substring(0, time.length - 2) + '00'
    return time
  },
  jqMiaoByTime(time) { // 截取时间的秒 (不带年月日的时间格式- 12:12:20变为20)
    time = time.substring(time.length - 2)
    return time
  },
  jqMinuteByTime(time) { // 截取时间的分钟 （格式- 2012-12-25 20:11:11）
    time = time.substring(time.length - 5, time.length - 3)
    return time
  },
  handleNum(start, end) { // 计算2个数差数的商(向上取整)
    let num = end - start + 1
    let snum = Math.ceil(num / minute)
    return snum
  },
  handleMinute (num) { // 把分钟转化成秒
    return num * minute
  },
  jsMoney (money, num) { // 计算成交额
    let sun = (num * perGu * money).toFixed(2)
    return sun
  },
  chengMoney (money, num) { // 计算成交额 -扩大10000倍
    let sun = money * benshu * perGu * num
    return sun
  },
  chuMoney (money) {  // 计算成交额 -缩小10000倍
    let sun = (money / benshu).toFixed(2)
    return sun
  }
}



