<!--委托成交流水-->
<template>
  <div class="EntrustingaDeal">
     <ul class="header">
         <li>
           <span>时间</span>
           <span>价格</span>
           <span>属性</span>
           <span>笔数</span>
           <span>手数</span>
         </li>
     </ul>
    <ul>
       <template v-for="a in EntrustingaDeal" >
         <li class="text" v-for="(b,index) in a.ChildList" >
             <span>{{index == 0 ? a.timestamp : ''}}</span>
             <span>{{index == 0 ? a.price : ''}}</span>
             <span>{{b.property}}</span>
             <span>{{b.StrokeCount}}</span>
             <span>{{b.Lots}}</span>
         </li>
       </template>
     </ul>
  </div>
</template>

<script>
  export default {
    name: 'EntrustingaDeal',
    data() {
      return {

      }
    },
    props: {
      EntrustingaDeal: Array
    },
    methods: {

    }

  }
  </script>


<style scoped lang='less' >
  html,body{
    height: 100%;
  }
  *{
    color: #a7a2a2;
    font-size:12px;
  }
  span{
    width: 20%;
    text-align: center;
  }
  span:nth-last-child(3){
    width: 30%;
  }
  ul{
    list-style: none;
  }
  [v-cloak]{
    display: none;
  }
  .EntrustingaDeal{
    max-height:609px;
    overflow-y: auto;
        ul{
          border-top:none;
          flex-direction: column;
          padding-left: 3px;
          padding-right: 3px;
          display:flex;
          flex:auto;
          padding-top: 30px;
          .text{
            display: flex;
            justify-content: space-between;
            line-height: 185%;
            flex: auto;
            -webkit-box-flex: 1;
            -ms-flex: auto;
          }
        }
        .header{
          position:fixed;
          height:30px;
          line-height:30px;
          background: #313335;;
          z-index: 1;
          width:298px;
          padding-top: 0;
          li{

              display: flex;
              justify-content: space-between;
              height: 30px;
              line-height: 30px;
              flex: auto;
              -webkit-box-flex: 1;
              -ms-flex: auto;

          }
        }

  }
</style>



